</template>

<script>
import transverse from './childre/transverse';
import scorll from '../../../components/scroll';
import {mapState,mapActions} from 'vuex'
export default {
    components:{
        [transverse.name]:transverse,
        [scorll.name]:scorll,
    },
    computed:{
        ...mapState({
            topicNavlist:state => state.Topic.topicNavlist,
            topicInformation:state => state.Topic.topicInformation,
        })
    },
    methods:{
        ...mapActions({
            requesTopicNavlist:'Topic/requesTopicNavlist',
            requesTopicInformation:'Topic/requesTopicInformation'
        }),
        //搜索页跳转
        routerAction(){
            this.$router.push('/search');
        }  
    },
    created(){
        //请求识物导航数据
        this.requesTopicNavlist();
        //请求页面具体数据
        this.requesTopicInformation();
    }
}
</script>

